import React, {Fragment, useEffect, useState} from "react";
import {CircularProgress, withStyles} from "@material-ui/core";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Icon from '@material-ui/core/Icon';
import styles from './styles';
import {Steps} from 'intro.js-react';
import 'intro.js/introjs.css';
import IframeComm from "react-iframe-comm";

const ManualBook = (props) => {
    const {classes} = props;

    /**********************************************************/
    /**/
    let isNewUser = localStorage.getItem("nemu-new-manual-book");
    let showSteps = true;
    if (isNewUser === null || isNewUser === undefined) {
        localStorage.setItem("nemu-new-manual-book", "true");
    } else {

        showSteps = false;
    }
    const [stepsEnabled, setStepsEnabled] = useState(showSteps);
    const initialStep = 0;
    const steps = [
        {
            element: '.lang-en',
            intro: 'Klik tombol ini untuk buku panduan berbahasa Inggris',
        },
        {
            element: '.lang-id',
            intro: 'Klik tombol ini untuk buku panduan berbahasa Indonesia',
        },
        {
            element: '.lang-download',
            intro: 'Klik tombol ini meng-unduh buku panduan',
        },
    ];
    const onExit = () => {
        setStepsEnabled(false);
    };
    /**/
    /**********************************************************/

    const links = {
        en: "1grk0l4S8fpOqt6sMzm23EQLNJHjUOpBV",
        id: "1a2yZiHmieI_lTlNR7xEOztXMBf5FzcJs"
    };
    const [selectedLang, setSelectedLang] = useState("");
    const [tempSelectedLang, setTempSelectedLang] = useState("");
    const [heightIframe, setHeightIframe] = useState("0px");

    const clickHandler = (selectLanguage) => {
        if (tempSelectedLang === selectLanguage)
            return;

        setHeightIframe("0px");
        let link = selectLanguage === "en" ? links.en : links.id;

        setSelectedLang("https://docs.google.com/viewer?srcid=" + link + "&pid=explorer&efh=false&a=v&chrome=false&embedded=true");

        setTempSelectedLang(selectLanguage);
    };

    useEffect(() => {
        clickHandler("en"); // eslint-disable-next-line
    }, []);

    return (
        <Fragment>
            <Steps
                enabled={stepsEnabled}
                steps={steps}
                initialStep={initialStep}
                onExit={onExit}
            />

            <br/>
            <div className={classes.container}>
                <Grid container spacing={0} alignItems={"center"} justify={"center"}>
                    <Grid item xs={12}>
                        <Typography align={"center"} gutterBottom variant="h4" component="h2">
                            <b style={{color: '#3f51b5'}}>Manual Book</b>
                        </Typography>
                    </Grid>
                    <div align="center">
                        <Grid item xs={12}>
                            <Button onClick={() => clickHandler("en")} variant="outlined"
                                    color="primary"
                                    className={[classes.button, "lang-en"].join(' ')}>
                                English
                            </Button>
                            <Button onClick={() => clickHandler("id")} variant="outlined"
                                    color="primary"
                                    className={[classes.button, "lang-id"].join(' ')}>
                                Bahasa Indonesia
                            </Button>
                            <Button
                                onClick={() => window.open("https://drive.google.com/open?id=1e244uyq-cKvPqX36JBYIfowVohFzd31d", "_blank")}
                                variant="contained" color="primary"
                                className={[classes.button, "lang-download"].join(' ')}>
                                <Icon>save_alt</Icon>
                            </Button>
                        </Grid>
                    </div>
                </Grid>
                <Grid container spacing={0} justify={"center"}
                      alignItems="center"
                      style={{minHeight: '60vh'}}>
                    {
                        heightIframe === "0px" ? (
                            <Grid container spacing={0} justify={"center"} alignItems="center" direction={"column"}>
                                <CircularProgress thickness={6} size={75}/>
                                <br/> <br/>
                                <Typography variant={"display1"}>
                                    <span role="img" aria-label="Face With Monocle">üßê</span> Taking it for you <span role="img" aria-label="Face With Monocle">üßê</span>
                                </Typography>
                            </Grid>
                        ) : null
                    }
                    <Grid item xs={12}>
                        <div className={classes.iframe}>
                            <IframeComm
                                attributes={{src: selectedLang, width: "100%", height: heightIframe}}
                                handleReady={() => setHeightIframe("100%")}
                            />
                        </div>
                    </Grid>
                </Grid>
            </div>
        </Fragment>
    );
}

export default withStyles(styles)(ManualBook);